/* global React ReactDOM FilterablePartnerEntries */

/*
 * fetch and re-render partners listings with latest data
 */
(function () {

	"use strict";

	const url = window.location.origin + "/partners/find/partners.json";
	fetch(url).then(res => res.json()).then(data => {

		window.qualys.utils.animateQualysHero();

		const element = document.getElementById("reactapp");
		ReactDOM.hydrate(
			React.createElement(
				FilterablePartnerEntries,
				{ "partners": data }
			),
			element
		);

	});

}());
